<template>
    <v-btn outlined color="#273746" @click="resetItems()">
        Resetear Items inferiores
    </v-btn>
</template>

<script>
export default {
    name: 'ResetSurveyItems',
    props: {
        configDefault: {
            type: [Array, Object],
            default: null,
        },

    },
    methods: {
        resetItems() {
            this.$emit("itemsResult", this.configDefault);
            this.$emit("show", false);
            setTimeout(() => {
                this.$emit("show", true);
            }, 1);
        }
    },
}
</script>